@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@200;300;400;500;600;700&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Readex Pro', sans-serif;
  background-image: linear-gradient(to  bottom right, #434343 0%, black 100%);
}

.container{
  max-width: 500px;
  min-width: 300px;
  margin: 30px auto;
  overflow: auto;
  border-radius: 15px;
  border: solid 2px rgb(25, 255, 167);
}

.container > h1{
  display:flex;
  justify-content: center;
  color:#fff;
  margin: 10px;
}
a{
  text-decoration: none;
  color: #000;
}

li{
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  margin: 15px;
  padding: 10px;
  background-color: #19a383;
  border-radius: 50px;
  transition: .3s;
  cursor: pointer;
}

li:hover{
  font-weight:700;
  background-color: #1ecea5;
}

li > div > img{
  margin-left: 5px;
}

.star-off{
  opacity: 0.5;
 
}

#footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  color: #fff;
  font-size: 14px;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #048365;
}

.logo{
  
  width: 150px;
}

.center{
  display: flex;
  justify-content: center;
}
